.skill-item {
    width: 100%;
    display: flex;
    margin: 10px auto;
}
.skill-item > div {
    margin-left: 20px;
    width: 100%;
    max-width: 400px;
}
.skill-item > img {
    width: 70px;
    height: 70px;
}
.skill-item h4, .skill-item p {
    margin-left: 5px;
    margin-bottom: 0;
}