#introduction {
    width: 100%;
    height: 100vh;
    background-image: url("../../assets/Waihou.svg");
    /* background-size: cover; */
    background-position: top;
    background-attachment: fixed;

}
#menu {
    position: relative;
}
#menu:hover {
    position: relative;

}
#menu > img {
    width: 70px;
    margin: 30px;
    cursor: pointer;
    transition-duration: 0.3s;
}
#menu > img:hover {
    transform: scale(1.1);
    transition-duration: 0.3s;    
}
#menu > img:active{
    transform: rotate(90deg);
}
.intro-content {
    height: 80vh;
}
.intro-content button {
    transition: .5s;
    width: 200px;
    height: 50px;
    background-color: var(--red);
    border: none;
    border-radius: 10px;
    outline: none;
    margin: 20px;
    margin-top: 100px;
    font-size: larger;
    font-family: 'Segoe Script', 'Seoge UI', Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
}
.intro-content button:hover {
    background-color: var(--green);
    color: white;
    transition: .5s;
    box-shadow: 1px 1px 10px var(--green);
}
.menu {
    position: absolute;
    top: 90px;
    left: 30px;
    display: flex;
    flex-direction: column;
    visibility: hidden;
    opacity: 0;
    transition: visibility 0s, opacity 0.2s linear;
}
.menu-open {
    visibility: visible;
    opacity: 1;
}
.menu a {
    color: rgb(255, 255, 255);
    font-size: larger;
    margin: 10px 0;
    height: 50px;
    padding: 10px;
    transition-duration: 0.3s;   
    border-radius: 20px; 
    text-align: center;
    cursor: pointer;
}
.menu a:hover {
    text-decoration: none;
    transform: scale(1.1);
    transition-duration: 0.3s;
}