@import url(//db.onlinewebfonts.com/c/5c452fb7a0f5751f4e94e9d84eb01154?family=Segoe+Script);
/* Segoe Script*/
:root {
	--red: #c50e0e6c;
	--green: #0ec52065;
	--yellow: #bda376;
	--purple: #7f76bd;
	--blue: #79cfcd;
	font-size: 15px;
}
.blue {
	color: var(--blue);
}
.green {
	color: var(--green);
}
.yellow {
	color: var(--yellow);
}
.purple {
	color: var(--purple);
}
.red {
	color: var(--red);
}
.fill-blue {
	fill: var(--blue);
}
.fill-green {
	fill: var(--green);
}
.fill-yellow {
	fill: var(--yellow);
}
.fill-purple {
	fill: var(--purple);
}
.fill-red {
	fill: var(--red);
}
.bg-blue {
	background-color: var(--blue);
}
.bg-green {
	background-color: var(--green);
}
.bg-yellow {
	background-color: var(--yellow);
}
.bg-purple {
	background-color: var(--purple);
}
.bg-red {
	background-color: var(--red);
}
h1,
h2,
h3,
h4,
h5,
h6 {
	font-family: 'Segoe Script', 'Seoge UI', Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
	color: grey;
}
p {
	font-family: 'Segoe UI', Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
	color: gray;

}

#about-me,
#education,
#projandintern,
#skills,
#toolsandtech,
#refrences {
	margin: 10% auto;
	padding: 10px;
}
#about-me > h3,
#education > h3,
#skills > h3,
#projandintern > h3,
#toolsandtech > h3,
#refrences > h3 {
	margin: 40px auto;
	text-align: center;
}
